<template>
  <el-row v-loading="loading">
    <el-form v-if="!loading" :model="setting" ref="form" label-width="100px">
      <el-form-item label="初始化时间">
        <el-input-number :controls="false" size="small" v-model="setting['initial_time']['time']" style="width: 100px" :disabled="!isAdmin"></el-input-number>
        <el-select v-model="setting['initial_time']['units']" style="width: 60px" size="small" :disabled="!isAdmin">
          <el-option label="时" value="hours"></el-option>
          <el-option label="分" value="minutes"></el-option>
          <el-option label="秒" value="seconds"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="循环时间">
        <el-input-number :controls="false" size="small" v-model="setting['interval']['time']" style="width: 100px" :disabled="!isAdmin"></el-input-number>
        <el-select v-model="setting['interval']['units']" style="width: 60px" size="small" :disabled="!isAdmin">
          <el-option label="时" value="hours"></el-option>
          <el-option label="分" value="minutes"></el-option>
          <el-option label="秒" value="seconds"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="推送列表">
        <xm-info-box title="群聊">
          <xm-tags :value.sync="setting['push_list']['group']" :disabled="!isAdmin"></xm-tags>
        </xm-info-box>
        <xm-info-box title="私聊">
          <xm-tags :value.sync="setting['push_list']['user']" :disabled="!isAdmin"></xm-tags>
        </xm-info-box>
      </el-form-item>
      <el-form-item label="订阅源">
        <xm-info-box :editable="isAdmin" retractable v-for="(item,key) of setting.rss" :key="key" :title.sync="item.title">
          <el-form label-width="80px" class="rss-form-box">
            <el-form-item label="启用" class="inline-block" style="width: 200px">
              <el-switch v-model="item.on" active-color="#13ce66" :disabled="!isAdmin"></el-switch>
            </el-form-item>
            <el-form-item label="启用代理" class="inline-block">
              <el-switch v-model="item.proxy" active-color="#13ce66" :disabled="!isAdmin"></el-switch>
            </el-form-item>
            <el-form-item label="源网址">
              <el-input v-model="item.source" :readonly="!isAdmin"><el-button slot="append" icon="el-icon-s-promotion" @click="sourceTest(item.source,item.proxy)" :loading="testLoading">测试</el-button></el-input>
            </el-form-item>
            <el-form-item label="键名">
              <el-input v-model="item.name" :readonly="!isAdmin"></el-input>
            </el-form-item>
            <el-form-item label="关键词">
              <xm-tags :value.sync="item['name_filter']" :disabled="!isAdmin"></xm-tags>
            </el-form-item>
            <el-form-item label="提取链接">
              <el-input v-model="item['link_replace']" :readonly="!isAdmin"></el-input>
            </el-form-item>
            <el-form-item label="推送列表" class="not-margin">
              <xm-info-box title="群聊">
                <el-checkbox :disabled="!isAdmin" :checked="item['push_group']==='all'||!item['push_group'] instanceof Array" @change="item['push_group']=$event?'all':[]" label="全局推送"></el-checkbox>
                <xm-tags v-if="item['push_group']!=='all'" :value.sync="item['push_group']" :disabled="!isAdmin"></xm-tags>
              </xm-info-box>
              <xm-info-box title="私聊">
                <el-checkbox :disabled="!isAdmin" :checked="item['push_user']==='all'||!item['push_user'] instanceof Array" @change="item['push_user']=$event?'all':[]" label="全局推送"></el-checkbox>
                <xm-tags v-if="item['push_user']!=='all'" :value.sync="item['push_user']" :disabled="!isAdmin"></xm-tags>
              </xm-info-box>
            </el-form-item>
          </el-form>
        </xm-info-box>
      </el-form-item>
    </el-form>
    <el-dialog :visible.sync="testVisible" :title="testTitle" center>
      <el-collapse class="test-box">
        <el-collapse-item title="body"><pre>{{testResult.body}}</pre></el-collapse-item>
        <el-collapse-item title="line"><pre>{{testResult.line}}</pre></el-collapse-item>
      </el-collapse>
    </el-dialog>
  </el-row>
</template>

<script>
  import * as api_rss from '../../api/rss'

  export default {
    name: "rss-setting",
    data() {
      return {
        isAdmin: false,
        setting: {},
        loading: false,
        testLoading: false,
        testVisible: false,
        testTitle:'',
        testResult:{body:'',line:''}
      }
    },
    methods: {
      sourceTest(source,proxy){
        this.false = true
        api_rss.rssSourceTest({source,proxy}).then(res=>{
          if (res['code']===0){
            this.testTitle = source
            this.testResult = res.data
            this.testVisible = true
          }else this.$message.error(res['msg'])
          this.testLoading = false
        }).catch(e=>{})
      },
      getJsonFormat(data){
        return data&&data!==''?JSON.stringify(data, null, 4):''
      }
    },
    created() {
      this.loading = true
      this.checkAdmin().then(this.isAdmin=true).catch(()=>{this.isAdmin=false})
      api_rss.getRssSetting().then(res => {
        this.setting = res.data
        this.loading = false
      }).catch(e=>{})
    }
  }
</script>

<style scoped>
  .rss-form-box>>>.el-form-item:not(.not-margin){
    margin: 10px 0;
  }
  .test-box>>>.el-collapse-item .el-collapse-item__content{
    height: 600px;
    width: 100%;
    overflow: auto;
  }
</style>



